				"html": "<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" alt=\":triangular_flag_on_post:\" title=\":triangular_flag_on_post:\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" alt=\":busts_in_silhouette:\" title=\":busts_in_silhouette:\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" alt=\":pencil:\" title=\":pencil:\"></p>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" alt=\":checkered_flag:\" title=\":checkered_flag:\"> Problem Explanation:</h3>\n<p>For example: <code>slasher([1, 2, 3], 2);</code> must return [3].</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href=\"http://forum.freecodecamp.com/t/javascript-array-prototype-splice/14307\">JS Array Prototype Splice</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/javascript-array-prototype-slice/14302\">JS Array Prototype Slice</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 1</h2>\n<ul>\n<li>We need only the remaining part of an array, so we can just remove what we don't.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 2</h2>\n<ul>\n<li><code>splice()</code> function can be used.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 3</h2>\n<ul>\n<li><code>slice()</code> function can be used.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"687474703a2f2f7777772e796f75726472756d2e636f6d2f796f75726472756d2f696d616765732f323030372f31302f31302f7265645f7761726e696e675f7369676e5f322e676966.gif\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" alt=\":beginner:\" title=\":beginner:\"> Basic Code Solution:</h2>\n<pre><code>function slasher(arr, howMany) {\n  // remove the head\n  arr.splice(0, howMany);\n  // return the remaining or the tail\n  return arr;\n}\nslasher([1, 2, 3], 2);\n</code></pre>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CLjU/28\">Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li>This solution uses the <code>splice()</code> function.</li>\n<li>First argument, <strong>arr</strong> is the array to be modified.</li>\n<li>Second argument, <strong>howMany</strong> is the number of elements to be removed starting with <strong>arr[0]</strong>.</li>\n<li>Modify the array with <code>splice()</code> and return it.</li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/sunflower.png?v=3\" alt=\":sunflower:\" title=\":sunflower:\"> Intermediate Code Solution:</h2>\n<pre><code>function slasher(arr, howMany) {\n\n  // Return string after the amount chopped off.\n  return arr.slice(howMany);\n\n}\n</code></pre>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CLjU/29\">Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li>This solution uses the <code>slice()</code> function.</li>\n<li>The argument <strong>howMany</strong> is the number of elements to be removed starting with <strong>arr[0]</strong>.</li>\n<li>Modify the array with <code>slice()</code> and return it.</li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" alt=\":clipboard:\" title=\":clipboard:\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" alt=\":warning:\" title=\":warning:\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories — <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" alt=\":traffic_light:\" title=\":traffic_light:\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" alt=\":warning:\" title=\":warning:\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" alt=\":point_right:\" title=\":point_right:\"> <a href=\"http://forum.freecodecamp.com/t/algorithm-article-template/14272\"><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>",
